import React, { Component } from "react";
import fire from "../../firebase";

class HeaderConfig extends Component {
  onlogoutClick() {
    console.log("helloo" , this.props)
    localStorage.removeItem('user')

    fire.auth().signOut().then(()=> {
      console.log("User has signed out!!!")
      this.props.history.replace('login')
    }).catch(e => {
      console.log("error has occured" , e)
    })
  }

  LoginRegHandler() {
    const offCanvasConfig = document.querySelector(".off-canvas-cog");
    offCanvasConfig.classList.add("active");
    document.querySelector("body").classList.add("fix");
  }

  MobileMenuHandler() {
    const offCanvasMenu = document.querySelector(".off-canvas-menu");
    offCanvasMenu.classList.add("active");
  }

  componentDidMount(){
      console.log("Header config started!!!")
  }

  
  render() { 
    let displayName = JSON.parse(localStorage.getItem("displayName"));
    console.log("i m use",displayName);
    return (
      <div className="header-action mt-lg-3 text-right">
      
        {/* <a href="tel:00199823568658" className="tel-no">+998 23568 658</a> */}
        {/* <button onClick={this.LoginRegHandler} className="btn-cog">
          <i className="fa fa-cog" />
        </button> */}
        
        <button onClick={this.MobileMenuHandler} className="btn-menu d-lg-none">
          <i className="fa fa-bars" />
        </button>
        <button 
        style={{
          "color": "#FFFFFF",
          "display": "block",
          "background":"#080b1a",
          "text-transform": "uppercase",
        }}
            type="button" 
            class="btn btn-dark"
            onClick={this.onlogoutClick.bind(this)}
            >
              logout
            </button>
      </div>
      
    );
  }
}

export default HeaderConfig;
