// import img from "../../assets/img/blog/01.jpg";
import React, { Component } from "react";
import fire from "../../firebase";

class ComplainboxItem extends Component {
  ondeletecomplainboxclick() {
    console.log("delete", this.props.complain);
    fire
      .firestore()
      .collection("complainbox")
      .doc(this.props.complain.id)
      .delete()
      .then(() => {
        console.log("Document successfully deleted!");
      })
      .catch((error) => {
        console.error("Error removing document: ", error);
      });
  }
  render() {
    return (
      <div
        className="col-12"
        style={{
          display: "flex",
          justifyContent: "center",
          marginBottom: "20px",
        }}
      >
        <div
          className="card"
          style={{
            width: "100%",
            maxWidth: "500px",
            alignContent: "center",
            alignItems: "center",
          }}
        >
          {/* <img src={img} /> */}
          <div className="card-body" style={{ "min-width": "100%" }}>
            <h5 className="card-title">{this.props.complain.studentname}</h5>
            <h5 className="card-title">{this.props.complain.roomno}</h5>
            <p className="card-text">{this.props.complain.complaindesc}</p>


            <button
              type="button"
              className="btn btn-outline-dark"
              onClick={this.ondeletecomplainboxclick.bind(this)}
            >
              Delete
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default ComplainboxItem;
